* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 400% 400%;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  animation: gradient 15s infinite;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

img {
  max-width: 100%;
  border-radius: 5px;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.header-image {
  text-align: center;
}

.section-gallery {
  margin: 0 auto;
  padding-top: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

.gallery-container {
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 40px;
  display: flex;
}

.gallery__item {
  border-radius: 5px;
  position: relative;
  overflow: hidden;
  box-shadow: 4px 6px 16px rgba(111, 15, 115, .2), 5px 6px 11px rgba(20, 1, 135, .85);
}

.info {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.thumb {
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.sl-image {
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 4px 6px 16px rgba(111, 15, 115, .2), 5px 6px 11px rgba(20, 1, 135, .85);
}

.photo-card {
  width: 300px;
  background-color: #f0f8ff;
  border-radius: 5px;
  flex-wrap: wrap;
  display: flex;
  overflow: hidden;
  box-shadow: 4px 6px 16px rgba(111, 15, 115, .2), 5px 6px 11px rgba(20, 1, 135, .85);
}

